import { useEffect, useState } from "react";
import Intro from "./Intro";

const { default: Main } = require("./Main");

function App() {
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    setTimeout(() => setIsLoading(true), 500);
  }, [isLoading]);

  return (
    <div className="App">
      {!isLoading && <p>LOADING</p>}
      {isLoading && <Intro />}
    </div>
  );
}

export default App;
